import React from 'react'
import './index.less'
import axios from 'axios'
import { baseUrl } from '../../config';
export default class Repos extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            products: [],
            lastProduct: '',
        }
    }
    componentDidMount() {
        // 获取商户信息
        axios.get(
            'http://www.almondlee.cn/api/get_products',
            {
                params: {
                    id_seller: this.props.id,
                    order: 'id',
                    lastProduct: this.state.lastProduct,
                    orderValue: this.state.lastProduct,
                    esc: 'true'
                }
            })
            .then((Response) => {
                //return data数据
                console.table('res', Response.data);
                // 追加products
                let products = this.state.products
                Response.data.products.map(i => {
                    products.push(i)
                })
                this.setState({
                    products: products,
                    lastProduct: Response.data.products[Response.data.products.length - 1].id,
                })
                console.table('state', this.state);

            })
            .catch((err) => {
                console.log(err);
            })

    }
    render() {
        return (
            <div className='goods'>
                <ul className="vproductShow-card-container">
                    {this.state.products.map(p =>
                        <li className="vproductShow-card" id={p.id}>
                            <img src={`${baseUrl}/${p.productDatas[0]}`} alt=''></img>
                            {p.product_name + p.type + p.price + p.productUrl}
                        </li>
                    )}
                </ul>
            </div>
        )

    }
}